@import "../global/mixins.scss";
@import "../global/variables.scss";

.form {
  display: flex;
  flex-direction: column;
  @include normalize-list;
}

.form__group {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  border: none;
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  gap: 14px;
  min-width: 0;

  &--purpose {
    gap: 16px;
  }
}



.form__control {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.form__group--cat .form__control {
  gap: 13px;
}

.form__group--cat .form__label {
  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
    color: #444444;
  }
}

.form__input[type="text"] {
  margin: 0;
  padding: 0;
  display: flex;
  flex-grow: 1;
  overflow: hidden;
  padding: 14px 20px 13px 20px;
  border: none;
  outline: 1px solid #E7E7E7;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
    color: #444444;
  }
}


.form__group--cat {
  margin-bottom: 46px;

  @media (min-width: $tablet-width) {
    margin-bottom: 72px;
  }
}

.form__list {
  display: flex;
  flex-direction: column;
  gap: 13px;
  @include normalize-list;
}

.form__list--purpose {
  margin-bottom: 45px;

  @media (min-width: $tablet-width) {
    padding: 25px 309px 25px 27px;
    outline: 1px solid #E7E7E7;
    margin-bottom: 75px;
  }
}

.form__list--purpose .form__control {
  gap: 15px;
}

.form__title {
  padding: 0;
  font-size: 24px;
  line-height: 26px;
  color: #000000;
}

.form__group--owner-cat {
  margin-bottom: 23px;
}

.form__group--owner-cat .form__title {
  margin-bottom: 35px;


  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 76px;
  }
}

.form__controls-owner-cat-wrapper {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.form__input[type="text"],
.form__input[type="email"],
.form__input[type="tel"] {
  margin: 0;
  padding: 0;
  display: flex;
  flex-grow: 1;
  overflow: hidden;
  padding: 14px 20px 13px 20px;
  border: none;
  outline: 1px solid #E7E7E7;
  text-transform: uppercase;
}

.input--comment {
  min-height: 150px;
}

.form__group--owner-cat .form__control {
  gap: 10px;
}

.form__group--comment {
  margin-bottom: 23px;
}

.form__group--comment .form__title {
  margin-bottom: 38px;
}

.form__group--additional {
  margin-bottom: 34px;
}

.form__group--additional .form__title {
  margin-bottom: 34px;
}

.form__list--additional {
  @include normalize-list;
}

.form__list--additional .form__control {
  display: flex;
  gap: 20px;
}

.form__button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form__button {
  margin-bottom: 21px;
}

.form__text {
  text-align: center;
}
